<template>
<div >
  <p>!</p>
  <div class="name" v-show="isshow">
  <b class="tips">,</b>
  <el-input v-model="students" placeholder="" class="nameinput"></el-input>
   <el-button @click="submit"></el-button>
   </div>
</div>
</template>
<script>
export default {
  name: "Thanks",
  data() {
    return {
      students: "",
      isshow: true
    };
  },
  methods: {
    submit() {
      console.log(this.students);

      if (this.students === "") {
        this.$message({
          message: "",
          type: "warning"
        });
      } else {
        fetch(
          "http://120.79.193.101/ApproximateComputing/applicant/applicant",
          {
            method: "post",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json"
            },
            body: JSON.stringify({
              name: this.students
            })
          }
        ).then(response => {
          return response.json().then(data => {
            if (data.code === 0) {
              this.isshow = false;
              this.$message({
                showClose: true,
                message: "",
                type: "success"
              });
            }
          });
        });
      }
    }
  }
};
</script>
<style scoped>
p {
  font-size: 100px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -140%);
}
.tips {
  color: red;
}
.nameinput {
  width: 200px;
}
.name {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -70%);
}
</style>



